import React from "react";
import logo from "./logo.svg";
import "./App.css";
import Parse from "parse/dist/parse.min.js";
import isElectron from "is-electron";

function App() {
  const [isDarkTheme, setDarkTheme] = React.useState(
    localStorage.getItem("isLightTheme") ? true : false
  );

  const [myAccount, setMyAccount] = React.useState([]);
  const [allTests, setAllTests] = React.useState([]);
  const [allResults, setAllResults] = React.useState([]);

  const [regName, setRegName] = React.useState("");
  const [regRole, setRegRole] = React.useState("");
  const [regGroup, setRegGroup] = React.useState("");
  const [regLogin, setRegLogin] = React.useState("");
  const [regPassword, setRegPassword] = React.useState("");

  const [login, setLogin] = React.useState("");
  const [password, setPassword] = React.useState("");

  const [pageIndex, setPageIndex] = React.useState(0);

  const [dataTest, setDataTest] = React.useState([]);
  const [currentIndex, setCurrentIndex] = React.useState(0);
  const [currentChoose, setCurrentChoose] = React.useState("");

  const [sendResultName, setSendResultName] = React.useState("");
  const [isSendTest, setIsSendTest] = React.useState(false);

  const [role, setRole] = React.useState([
    { name: "Преподаватель" },
    {
      name: "Студент",
    },
  ]);

  const [group, setGroup] = React.useState([
    { name: "БИ-23" },
    {
      name: "ИСиТ-23",
    },
  ]);

  React.useEffect(() => {
    getValue();
  });

  const changeTheme = () => {
    store();
    getValue();
  };

  const store = () => {
    localStorage.setItem("isLightTheme", isDarkTheme);
  };

  const getValue = () => {
    let bodyClass = document.body.classList,
      htmlClass = document.documentElement.classList;

    if (!localStorage.isLightTheme) {
      store();
    }
    if (localStorage.getItem("isLightTheme") === "true") {
      bodyClass.add("light-theme");
      htmlClass.add("light-theme");
      setDarkTheme(false);
    } else if (localStorage.getItem("isLightTheme") === "false") {
      bodyClass.remove("light-theme");
      htmlClass.remove("light-theme");
      setDarkTheme(true);
    }
    // if (localStorage.getItem("isMessageTheme") === "false") {
    //   setMessageTheme(false);
    // }
  };

  async function fetchAccount() {
    const query = new Parse.Query("Accounts");

    query.equalTo("login", login);
    query.equalTo("password", password);

    const Person = await query.first();

    if (Person !== undefined) {
      setMyAccount(Person);
      setScreen(2);
    } else {
      console.log("user_not found");
    }
  }

  async function fetchTests() {
    const query = new Parse.Query("Tests");
    const tests = await query.findAll();

    setAllTests(tests);
  }

  async function fetchAllResults() {
    const query = new Parse.Query("Results");
    const result = await query.findAll();

    setAllResults(result);
  }

  async function createAccount() {
    try {
      const Person = new Parse.Object("Accounts");

      Person.set("name", regName);
      Person.set("group", regGroup);
      Person.set("login", regLogin);
      Person.set("password", regPassword);

      await Person.save();
    } catch (error) {
      console.log("Error saving new person: ", error);
    }
  }

  async function createTest() {
    try {
      const Test = new Parse.Object("Tests");

      Test.set("name", formTestName);
      Test.set("group", formTestGroup);
      Test.set("data", createTestData);

      await Test.save();
    } catch (error) {
      console.log("Error saving new test: ", error);
    }
  }

  const switchScreen = () => {
    switch (pageIndex) {
      case 0:
        return loginScreen();
      case 1:
        return regScreen();
      case 2:
        return profileScreen();
      default:
        return loginScreen();
    }
  };

  const setScreen = (id) => {
    setPageIndex(id);
  };

  const CustomDropdown = ({ text, value, array, setSelect }) => {
    const [isShow, setIsShow] = React.useState(false);
    return (
      <div className="custom-dropdown">
        <div
          className={
            value === "" ? "custom-dropdown-none" : "custom-dropdown-text"
          }
          onClick={() => setIsShow(!isShow)}
        >
          {isShow ? "-" : "+"} {value === "" ? `${text}...` : value}
        </div>

        <div
          className={`custom-dropdown-list ${isShow ? "is-active" : "none"}`}
        >
          {array.map((a) => {
            return (
              <button
                key={a.name}
                className="button special"
                onClick={() => setSelect(a.name)}
              >
                {a.name}
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  const regScreen = () => {
    return (
      <div className="content-login">
        <div className="text-title">Регистрация</div>
        <input
          placeholder="Логин"
          value={regLogin}
          onChange={(e) => setRegLogin(e.currentTarget.value)}
        />
        <input
          placeholder="Пароль"
          value={regPassword}
          onChange={(e) => setRegPassword(e.currentTarget.value)}
        />
        <input
          placeholder="ФИО"
          value={regName}
          onChange={(e) => setRegName(e.currentTarget.value)}
        />
        <CustomDropdown
          text={"Выбрать роль"}
          value={regRole}
          array={role}
          setSelect={setRegRole}
        />

        <CustomDropdown
          text={"Выбрать группу"}
          value={regGroup}
          array={group}
          setSelect={setRegGroup}
        />
        {/* <input
          placeholder="Группа"
          value={regGroup}
          onChange={(e) => setRegGroup(e.currentTarget.value)}
        /> */}
        <button className="button" onClick={() => setScreen(0)}>
          Есть аккаунт?
        </button>
        <button className="button primary" onClick={createAccount}>
          Создать аккаунт
        </button>
      </div>
    );
  };

  const loginScreen = () => {
    return (
      <div className="content-login">
        <div className="text-title">Вход в аккаунт</div>
        <input
          value={login}
          onChange={(e) => setLogin(e.currentTarget.value)}
        />
        <input
          value={password}
          onChange={(e) => setPassword(e.currentTarget.value)}
        />
        <div className="center">
          <button onClick={() => setScreen(1)} className="button">
            Нет аккаунта?
          </button>
          <button onClick={fetchAccount} className="button primary">
            Войти
          </button>
        </div>
      </div>
    );
  };

  const [isShowResult, setIsShowResult] = React.useState(false);

  const [count, setCount] = React.useState(0);

  const nextQuestion = (size, right) => {
    if (currentChoose === right) {
      setCount(count + 1);
    }

    if (currentIndex < size) {
      setCurrentIndex((a) => a + 1);
    } else {
      setIsShowResult(true);
    }
  };

  async function sendTestResult() {
    try {
      const Test = new Parse.Object("Results");

      Test.set("testName", sendResultName);
      Test.set("group", myAccount.get("group"));
      Test.set("userName", myAccount.get("name"));
      Test.set(
        "score",
        Number(((count * 100) / Number(dataTest[0].question.length)).toFixed(2))
      );

      await Test.save();

      setDataTest([]);
    } catch (error) {
      console.log("Error saving new test: ", error);
    }
  }

  const RadioCheck = ({ ans }) => {
    return ans !== "" ? (
      <div>
        <button
          onClick={() => setCurrentChoose(ans)}
          className={`custom-radio ${ans === currentChoose ? "is-active" : ""}`}
        >
          {ans}
        </button>
      </div>
    ) : null;
  };

  const testSection = () => {
    return (
      <div>
        {dataTest.length > 0 ? (
          dataTest.map((d, index) => {
            return (
              <div key={d.title} className="test-content">
                <h3 className="no-margin-top">{d.title}</h3>
                <div className="line"></div>
                <div>
                  {d.question.map((q, index) => {
                    return currentIndex === index ? (
                      <div key={q.name}>
                        <div className="text-sub-title">{q.name}</div>
                        {q.answer.map((ans, index) => {
                          return <RadioCheck key={index} ans={ans} />;
                        })}
                        <div>
                          <div className="line"></div>
                          <button
                            className={`button ${
                              currentIndex < d.question.length - 1
                                ? ""
                                : "primary"
                            }`}
                            onClick={() =>
                              nextQuestion(d.question.length - 1, q.right)
                            }
                          >
                            {currentIndex < d.question.length - 1
                              ? "Далее"
                              : "Завершить"}
                          </button>

                          {isShowResult ? (
                            <div>
                              <div className="line"></div>
                              <div className="text-sub-title margin-left">
                                Ваш результат:{" "}
                                <b>
                                  {(
                                    (count * 100) /
                                    Number(d.question.length)
                                  ).toFixed(2)}
                                </b>
                              </div>
                              <button
                                className="button primary"
                                onClick={sendTestResult}
                              >
                                Отправить тест преподавателю
                              </button>
                            </div>
                          ) : null}
                        </div>
                      </div>
                    ) : null;
                  })}
                </div>
              </div>
            );
          })
        ) : (
          <h3>У вас не начато ни одного теста</h3>
        )}
      </div>
    );
  };

  const [formTestName, setFormTestName] = React.useState("");
  const [formTestGroup, setFormTestGroup] = React.useState("ИСиТ-23 маг");
  const [formTestQuest, setFormTestQuest] = React.useState("");
  const [formTestChoose1, setFormTestChoose1] = React.useState("");
  const [formTestChoose2, setFormTestChoose2] = React.useState("");
  const [formTestChoose3, setFormTestChoose3] = React.useState("");
  const [formTestChoose4, setFormTestChoose4] = React.useState("");
  const [formTestChoose5, setFormTestChoose5] = React.useState("");
  const [formTestRight, setFormTestRight] = React.useState("");

  const [createTestData, setCreateTestData] = React.useState([]);
  const [questions, setQuestions] = React.useState([]);

  const addQuestion = () => {
    const array = [
      ...questions,
      {
        name: formTestQuest,
        answer: [
          formTestChoose1,
          formTestChoose2,
          formTestChoose3,
          formTestChoose4,
          formTestChoose5,
        ],
        right: formTestRight,
      },
    ];
    setQuestions(array);
  };

  const deleteQuestion = (id) => {
    const newQuestions = [...questions];
    newQuestions.splice(id, 1);
    setQuestions(newQuestions);
  };

  const completedTest = () => {
    const array = [
      {
        title: formTestName,
        question: questions,
      },
    ];
    setCreateTestData(array);
    setIsSendTest(true);
  };

  const createNewTest = () => {
    return (
      <div className="sub-content">
        <div className="text-title">Создать новый тест</div>
        <input
          placeholder="Название теста"
          value={formTestName}
          onChange={(e) => setFormTestName(e.currentTarget.value)}
        />
        <input
          placeholder="Название вопроса"
          value={formTestQuest}
          onChange={(e) => setFormTestQuest(e.currentTarget.value)}
        />

        <div>
          <h4>Варианты выбора</h4>
          <input
            placeholder="Вариант 1"
            value={formTestChoose1}
            onChange={(e) => setFormTestChoose1(e.currentTarget.value)}
          />
          <input
            placeholder="Вариант 2"
            value={formTestChoose2}
            onChange={(e) => setFormTestChoose2(e.currentTarget.value)}
          />
          <input
            placeholder="Вариант 3"
            value={formTestChoose3}
            onChange={(e) => setFormTestChoose3(e.currentTarget.value)}
          />
          <input
            placeholder="Вариант 4"
            value={formTestChoose4}
            onChange={(e) => setFormTestChoose4(e.currentTarget.value)}
          />
          <input
            placeholder="Вариант 5"
            value={formTestChoose5}
            onChange={(e) => setFormTestChoose5(e.currentTarget.value)}
          />
        </div>
        <input
          placeholder="Правильный ответ"
          value={formTestRight}
          onChange={(e) => setFormTestRight(e.currentTarget.value)}
        />
        <div>
          <button className="button" onClick={addQuestion}>
            Добавить вопрос
          </button>
          <button className="button primary" onClick={completedTest}>
            Завершить
          </button>

          {isSendTest ? (
            <button className="button primary" onClick={createTest}>
              Опубликовать тест
            </button>
          ) : null}
        </div>

        <div className="line"></div>
        <div>
          <h2>Предосмотр</h2>
          <h4>{formTestName}</h4>
          {questions.length > 0 ? (
            questions.map((d, index) => {
              return (
                <div key={d.name} className="preview-item">
                  <div className="preview-item-id">№ {index + 1}</div>
                  <div className="text-sub-normal">
                    Название вопроса: {d.name}
                  </div>
                  <div className="text-sub-normal">
                    Правильный ответ: {d.right}
                  </div>
                  {d.answer.map((ans, index) => {
                    return ans !== "" ? (
                      <div key={index}>
                        <div className="text-sub-normal">- {ans}</div>
                      </div>
                    ) : null;
                  })}
                  <button
                    className="preview-item-delete"
                    onClick={() => deleteQuestion(index)}
                  >
                    Удалить
                  </button>
                </div>
              );
            })
          ) : (
            <div className="text-sub-title no-margin">
              Вы еще не добавили ни одного теста
            </div>
          )}
        </div>
        <div></div>
      </div>
    );
  };

  const startTest = (data, name) => {
    setDataTest(data);
    setSendResultName(name);
  };

  const signOut = () => {
    setScreen(0);
    setMyAccount([]);
  };

  const minBtn = () => {
    if (isElectron()) {
      const { ipcRenderer } = window.require("electron");
      ipcRenderer.send("app-minimize");
    }
  };

  const maxBtn = () => {
    if (isElectron()) {
      const { ipcRenderer } = window.require("electron");
      ipcRenderer.send("app-maximize");
    }
  };
  const closeBtn = () => {
    if (isElectron()) {
      const { ipcRenderer } = window.require("electron");
      ipcRenderer.send("app-close");
    }
  };

  const profileScreen = () => {
    return (
      <div>
        <header className="sub-content header">
          <div className="one-line">
            <div>
              <div className="profile-title">{myAccount.get("name")}</div>
              <div className="profile-title-sub">{myAccount.get("role")}</div>
            </div>

            <div>
              <button className="button" onClick={changeTheme}>
                Сменить тему
              </button>
              <button className="button out" onClick={signOut}>
                {">"}
              </button>
            </div>
          </div>
        </header>

        <div className="sub-content">
          <div className="one-line">
            <div className="text-title no-margin">Список тестов</div>
            <button className="button primary no-margin" onClick={fetchTests}>
              Обновить
            </button>
          </div>
          <div className="line"></div>
          <div>
            {allTests.length > 0 ? (
              allTests.map((t) => {
                return (
                  <div key={t.id} className="result-item">
                    <div className="one-line">
                      <div>{t.get("name")}</div>

                      {myAccount.get("role") !== role[0].name ? (
                        <button
                          className="button"
                          onClick={() =>
                            startTest(t.get("data"), t.get("name"))
                          }
                        >
                          Пройти тест
                        </button>
                      ) : null}
                    </div>
                  </div>
                );
              })
            ) : (
              <div className="text-sub-title no-margin">
                Обновите результаты
              </div>
            )}
          </div>
        </div>

        {myAccount.get("role") === role[0].name ? (
          <div className="sub-content">
            <div className="one-line">
              <div className="text-title no-margin">Список результатов</div>
              <button
                className="button primary no-margin"
                onClick={fetchAllResults}
              >
                Обновить
              </button>
            </div>
            <div className="line"></div>
            <div>
              {allResults.length > 0 ? (
                allResults.map((t) => {
                  return (
                    <div key={t.id} className="result-item">
                      <div>Название теста: {t.get("testName")}</div>
                      <div className="line"></div>
                      <div>ФИО: {t.get("userName")}</div>
                      <div className="line"></div>
                      <div>Группа: {t.get("group")}</div>
                      <div className="line"></div>
                      <div>Результат: {t.get("score")}</div>
                    </div>
                  );
                })
              ) : (
                <div className="text-sub-title no-margin">
                  Обновите результаты
                </div>
              )}
            </div>
          </div>
        ) : null}

        {myAccount.get("role") === role[0].name ? (
          <div>
            <div>{createNewTest()}</div>
          </div>
        ) : (
          <div className="sub-content">{testSection()}</div>
        )}
      </div>
    );
  };

  console.log(isElectron());

  return (
    <div className="App">
      <div
        className={`title-bar ${isElectron() ? "" : "title-hide"} draggable`}
      >
        <div className="title-bar-left">
          <div className="title-bar-name">Онлайн обучение</div>
        </div>
        <div className="title-bar-right">
          <button
            id="min-btn"
            onClick={minBtn}
            className="title-bar-button no-draggable"
          >
            <svg width="10px" height="5px">
              <image
                x="0px"
                y="0px"
                width="10px"
                height="1px"
                xlinkHref="data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAABAQAAAADPjgLTAAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAAAmJLR0QAAd2KE6QAAAAHdElNRQflBxATJzCKGDT+AAAAC0lEQVQI12P4fwAAAsEBwIB8nmYAAAAASUVORK5CYII="
              />
            </svg>
          </button>
          <button
            id="max-btn"
            onClick={maxBtn}
            className="title-bar-button no-draggable"
          >
            <svg width="10px" height="10px">
              <image
                x="0px"
                y="0px"
                width="10px"
                height="10px"
                xlinkHref="data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAQAAAAnOwc2AAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAAAmJLR0QA/4ePzL8AAAAHdElNRQflBxATJjJ9DWSTAAAAIUlEQVQI12P8/58BAzAxfGRgRIMfmRgYsKkcSEFGbI4HACQKBfnSevKYAAAAAElFTkSuQmCC"
              />
            </svg>
          </button>
          <button
            id="close-btn"
            onClick={closeBtn}
            className="title-bar-button no-draggable"
          >
            <svg width="10px" height="10px">
              <image
                x="0px"
                y="0px"
                width="10px"
                height="10px"
                xlinkHref="data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAQAAAAnOwc2AAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAAAmJLR0QA/4ePzL8AAAAHdElNRQflBxATLDPw5byPAAAAQklEQVQI123Oyw0AIAhEwY21If139DyAG0yUg2QIHwEbjQgQ+XAAKcQ2B5Co6sVNF4ub0NJ8+He7Z7+LOptk1u/4A3MrdKxL0Sj2AAAAAElFTkSuQmCC"
              />
            </svg>
          </button>
        </div>
      </div>

      <div className={`app-main-content ${isElectron() ? "" : "no-electron"}`}>
        {switchScreen()}
      </div>
    </div>
  );
}

export default App;
